<template>
  <div class="promotional-video">
    <div class="index-con">
      <div class="logo-img">
        <!-- <el-image :src="tail.img_path" class="logo" /> -->
        <span class="iconfont logo">&#xe6d9;</span>
      </div>
      <div class="index-text">
        <p>{{ tail.title }}</p>
        <p>{{ tail.content }}</p>
      </div>
    </div>
  </div>
</template>
<script>
import { GET_TAIL } from "@/api/mall";
export default {
  name: "mall",
  data() {
    return {
      tail: {}
    };
  },
  beforeCreate() {
    this.$emit("header", false);
    this.$emit("footer", false);
  },
  methods: {
    getTail() {
      GET_TAIL()
        .then(res => {
          this.tail = res.data;
          console.log(res.data);
        })
        .catch(err => {});
    }
  },
  mounted() {
    this.getTail();
    setTimeout(() => {
      this.$router.push("/top");
    }, 5000);
  }
};
</script>

<style scoped lang="scss" type="text/scss">
.promotional-video {
  position: fixed;
  background: rgb(0, 0, 0);
  width: 100%;
  top: 0;
  left: 0;
  height: 100%;
  .index-con {
    position: relative;
    width: 30%;
    height: 200px;
    top: 45%;
    margin: auto;
    overflow: hidden;
  }
  .logo-img {
    animation: 2s first-opacity linear;
    .logo {
      position: absolute;
      top: 0;
      left: 30%;
      animation: 1.8s left-slide linear 0.5s forwards;
    }
  }
  .index-text {
    position: absolute;
    top: 0;
    left: 110%;
    width: 100%;
    height: 40px;
    animation: 1.8s left-text linear 2s forwards;
    p {
      color: white;
      &:nth-child(1) {
        font-size: 50px;
        font-weight: bold;
        padding-bottom: 10px;
      }
      &:nth-child(2) {
        font-size: 28px;
      }
    }
  }
  .logo {
    font-size: 80px;
    color: red;
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .promotional-video {
    position: fixed;
    background: rgb(0, 0, 0);
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    .index-con {
      position: relative;
      width: 95%;
      height: 200px;
      top: 35%;
      margin: auto;
      overflow: hidden;
    }
    .logo-img {
      animation: 2s first-opacity linear;
      .logo {
        position: absolute;
        top: 0;
        left: 150%;
        animation: 2s left-slide linear 1s forwards;
      }
    }
    .index-text {
      position: absolute;
      top: 0;
      left: 110%;
      width: 100%;
      height: 40px;
      animation: 2s left-text linear 2.5s forwards;
      p {
        color: white;
        &:nth-child(1) {
          font-size: 50px;
          font-weight: bold;
          padding-bottom: 10px;
        }
        &:nth-child(2) {
          font-size: 28px;
        }
      }
    }
  }
}
</style>



